/* Functional styling;
 * These styles are required for noUiSlider to function.
 * You don't need to change these rules to apply your design.
 */

.nouislider-wrap {
  display: flex;
  align-items: center;

  input {
    flex-shrink: 0;
    max-width: 60px;
    height: 40px;
    padding: 0.3em 0.7em;
    text-align: right;
    font-size: 14px;
    border: 2px solid #cdd2d4;
    border-radius: $border-radius;
  }

  .noUi-target {
    flex-grow: 1;
    margin: 0 10px;
    min-width: 125px;
  }
}

.noUi-target,
